<template>
  <div class="container">
    <div class="row">
      <div @click="showDetail" class="add-btn">
        <i>+</i>
      </div>
    </div>
    <div class="chart">
        <div id="myChart" style="width: 100%; height:400px;"></div>
    </div>
    <div v-show="detailShow" class="detail" transition="fade">
      <div class="detail-main">
        <input class="input" size="11" type="text" v-model="calendar.items.text" @click.stop="open($event)" placeholder="请选择日期…" />
        <Calendar :show="calendar.show" :type="calendar.items.type" :value="calendar.items.value" :x="calendar.x" :y="calendar.y"
            :begin="calendar.items.begin" @cancel="dateCancel" @ok="dateOk" :end="calendar.items.end" :sep="calendar.items.sep"
            :single="calendar.items.single" :rangeValue='calendar.items.rangeValue' :autoclose='calendar.items.autoclose'>
        </Calendar>
        <span class="name">{{newWeight}}&nbsp;kg</span>
        <i class="detail-add" @click="addWeight">+</i>
      </div>
      <div class="detail-input">
        <div class="detail-line">
          <button @click="demo(1,1)">1</button>
          <button @click="demo(1,2)">2</button>
          <button @click="demo(1,3)">3</button>
        </div>
        <div class="detail-line">
          <button @click="demo(1,4)">4</button>
          <button @click="demo(1,5)">5</button>
          <button @click="demo(1,6)">6</button>
        </div>
        <div class="detail-line">
          <button @click="demo(1,7)">7</button>
          <button @click="demo(1,8)">8</button>
          <button @click="demo(1,9)">9</button>
        </div>
        <div class="detail-line">
          <button @click="demo(2,null)">C</button>
          <button @click="demo(1,0)">0</button>
          <button @click="demo(3,null)">.</button>
        </div>
      </div>
      <div class="detail-close" @click="hideDetail">
        <i class="icon-close">×</i>
      </div>
    </div>
  </div>
</template>

<script>
import Store from '@/localstorage'
import Calendar from './calendar'
var myChart
// function quickSort (arr) {
//   if (arr.length <= 1) { return arr }
//   let pivotIndex = Math.floor(arr.length / 2)
//   let pivot = arr.splice(pivotIndex, 1)[0]
//   let left = []
//   let right = []
//   for (let i = 0; i < arr.length; i++) {
//     if (Number(arr[i][0].split('/').join('')) <= Number(pivot[0].split('/').join(''))) {
//       left.push(arr[i])
//     } else {
//       right.push(arr[i])
//     }
//   }
//   return quickSort(left).concat([pivot], quickSort(right))
// }
export default {
  components: {
    Calendar
  },
  data () {
    return {
      msg: 'fit',
      chart: null,
      detailShow: false,
      newWeight: '',
      inputValue: 0,
      weight: Store.fetch('notebook-fit') || [],
      calendar: {
        show: false,
        x: 0,
        y: 0,
        items: {
          type: 'date',
          begin: '2015/08/20',
          end: [new Date().getFullYear(), new Date().getMonth() + 1, new Date().getDate()].join('/'),
          value: [new Date().getFullYear(), new Date().getMonth() + 1, new Date().getDate()].join('/'),
          text: [new Date().getFullYear(), new Date().getMonth() + 1, new Date().getDate()].join('/'),
          sep: '/',
          single: true,
          autoclose: true,
          weeks: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
          months: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
        }
      },
      option: {
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'time',
          // boundaryGap: false,
          nameTextStyle: {color: 'rgba(128, 128, 128, 0.5)'},
          splitLine: {
            show: false
          }
        },
        yAxis: {
          type: 'value',
          boundaryGap: [0, '100%'],
          splitLine: {
            show: false
          }
        },
        series: [{
          type: 'line',
          showSymbol: false,
          hoverAnimation: false,
          data: this.weight,
          markPoint: {
            data: [
              {type: 'max', name: '最大值'},
              {type: 'min', name: '最小值'}
            ]
          }
        }]
      }
    }
  },
  mounted () {
    myChart = this.$echarts.init(document.getElementById('myChart'))
    myChart.setOption(this.option)
    myChart.setOption({
      series: [{
        data: this.weight
      }]
    })
  },
  methods: {
    showDetail () {
      this.detailShow = true
    },
    hideDetail () {
      this.detailShow = false
      this.newWeight = ''
    },
    addWeight () {
      this.inputValue = parseFloat(this.newWeight)
      console.log(typeof this.calendar.items.value, this.calendar.items.value)
      if (isNaN(this.inputValue)) {
        this.detailShow = false
        this.newWeight = ''
      } else {
        // if (this.weight.length > 0 && this.weight[this.weight.length - 1][0] === this.calendar.items.value) {
        //   this.weight.pop()
        // }
        this.weight.push([this.calendar.items.value, this.inputValue])
        // quickSort(this.weight)
        console.log(this.weight)
        myChart.setOption({
          series: [{
            data: this.weight
          }]
        })
        this.detailShow = false
        this.newWeight = ''
      }
    },
    demo (tip, value) {
      if (tip === 1) {
        if ((value !== 0 || this.newWeight.length !== 0) && this.newWeight.indexOf('.') !== this.newWeight.length - 4) {
          this.newWeight = this.newWeight + value.toString()
        }
      } else if (tip === 2) {
        this.newWeight = ''
      } else if (tip === 3) {
        if (this.newWeight.indexOf('.') === -1) {
          this.newWeight = this.newWeight + '.'
        }
      }
    },
    open (e, type) {
      // 设置类型
      this.calendar.show = true
      this.calendar.x = e.target.offsetLeft
      this.calendar.y = e.target.offsetTop + e.target.offsetHeight + 8
    },
    dateCancel (obj) {
      this.calendar.show = obj.show
    },
    dateOk (obj) {
      this.calendar.items.text = obj.values[0]
      this.calendar.items.value = obj.values[0]
    }
  },
  watch: {
    weight: {
      handler: function (weight) {
        Store.save('notebook-fit', weight)
      },
      deep: true
    }
  }
}
</script>

<style scoped>
.container {
  color: black;
  width: 100%;
  height: 100%;
  overflow-y: hidden;
}
.add-btn {
  margin: 20px 15px 0 0;
  float: right;
  font-style: normal; 
  width: 40px;
  height: 40px;
  color:rgba(0, 111, 111, 0.5);
  background: rgba(255,255,255, 0.6);
  text-align: center;
  font-family: 'Times New Roman';
  line-height: 40px; 
  font-size: 3em;
  font-weight: bolder;
  border-radius: 20px 20px;
}
.chart {
  overflow-x: hidden;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.detail {
  position: relative;
  color: #fff;
  background: rgba(7, 17, 27, 0.6);
  width: 100%;
  min-height: 6000px;
}
.detail-main {
  height: 100px;
  line-height: 100px;
  width: 100%;
}
.name {
  position: absolute;
  top: -8px;
  right: 35%;
  height: 40px;
  font-size: 1.3em;
  text-align: center;
  color: rgba(255,255,255, 0.8);
}
.detail-add {
  position: absolute;
  top: 20px;
  right: 15px;
  font-style: normal; 
  width: 40px;
  height: 40px;
  color:rgba(7, 17, 27, 0.5);
  background: rgba(255,255,255, 0.8);
  font-family: 'Times New Roman';
  line-height: 40px; 
  font-size: 3em;
  font-weight: bolder;
  border-radius: 20px 20px;
}
.detail-input {
  width: 80%;
  margin: 0 auto;
}
.detail-line{
  width: 100%;
  display: flex;
  justify-content: center;
}
button {
  float: left;
  display: block;
  flex: 33%;
  font-size: 26px;
  padding-top: 20px;
  color: rgba(255,255,255, 0.7);
  background-color: transparent;
  border-style: none;
  outline:none;
}
button:hover {
  color: rgba(255,255,255, 1);
}
.detail-close {
  clear: both;
  font-size: 32px;
}
.icon-close {
  margin: 0 auto;
  margin-top: 8%;
  margin-left: 44%;
  float: left;
  font-style: normal; 
  width: 40px;
  height: 40px;
  color:rgba(7, 17, 27, 0.5);
  background: rgba(255,255,255, 0.8);
  text-align: center;
  font-family: 'Times New Roman';
  line-height: 40px; 
  font-size: 1.5em;
  font-weight: bolder;
  border-radius: 20px 20px;
}
.input{
  position: absolute;
  top: 15px;
  left: 5px;
  height: 40px;
  font-style: normal; 
  border-radius: 2px;
  padding:5px;
  font-size: 0.9em;
  background:url(data:image/svg+xml;base64,) no-repeat 8px 17px;
  padding-left: 30px;
  color: rgba(255,255,255, 0.8);
}
</style>



